.card2{
    width: 90%;
    min-width: 700px;
    border-radius: var(--border-radius);
    border: var(--border);
    background-color: var(--color4);
    padding: 20px 20px 10px 20px;
    margin: 0px 5%;
    margin-bottom: 20px;
    -webkit-box-shadow: var(--box-shadow);
    -moz-box-shadow: var(--box-shadow);
    box-shadow: var(--box-shadow);
}
.card2:last-child{
    margin-bottom: 0px;
}
.card2-pt1{
    width: 100%;
    height: 188px;
}
.card2-pt2{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 0px 20px;
}
.card2-person{
    width: 60px;
    height: 60px;
    background-image: url(../../utilities/profile.jfif);
    background-position: center;
    background-size: cover;
    border-radius: 50%;
}
.card2-name{
    margin: 0px 10px;
}
.card2-info{
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content:space-between;
}
.card2-info-pt1{
    display: flex;
    align-items: center;
    justify-content: start;
}
.card2-aditional-info{
    width: 50px;
    height: 25px;
    color: white;
    background-color: var(--color1);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    margin-right: 5px;
}
.card2Asi{
    position: relative;
    top:-14px;
}
.card2-stars{
    width: 110px;
    height: 20px;
    display: flex;
    justify-content: space-evenly;
}
.card2-fecha{
    font-size: 10px;
    margin-top: 5px;
    text-align: center;
    
}
.card2-stars div{
    width: 20px;
    height: 20px;
    background-color: var(--color5);
    clip-path: var(--cliphy-star);
}
.card2-description{
    height: 105px;
    overflow-y: auto;
    margin-top: 10px;
    margin-bottom: 0px;
}
.card2-aditional-info span{
    width: 20px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotate(-135deg);
    margin-left: -2px;
}
.card2-stats,.card2-reviews{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 80px;
}
.card2-interactions{
    width: 100%;
    height: 20px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.card2-interactions span{
    height: 30px;
    padding-top: 8px;
    position: relative;
}
.card2-interactions span p{
    font-size: 11px;
    width: 35px;
    height: 15px;
    border-radius: 20px;
    background-color: var(--color3);
    color: var(--color4);
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px;
    padding: 0px;
    position: absolute;
    top:-8px;
    right: -20px;
}
.card2-interactions span p:nth-child(1){
    top:0px;
}
.card2-input{
    width: 100%;
    position: relative;
    height: 25px;
    margin-bottom: 10px;
    margin-top: 16px;
}
.card2-containerComments input{
    width: 100%;
    outline: none;
    background-color: var(--color2);
    border: var(--border);
    border-radius: var(--border-radius);
    padding: 4px 7px;
}
.card2-input .icon{
    position: absolute;
    top: 4px;
    right: 5px;
}
.card2-coment1,.card2-coment2{
    margin-bottom: 10px;
}
.card2-MostrarMas{
    font-size: 11px;
    margin-top: -10px;
    margin-bottom: 0px;
}
.card2-MostrarMas a:hover{
    text-decoration: underline;
    cursor: pointer;
}
.card2-MostrarMas span{
    font-size: 15px;
    position: relative;
    top:3px;
}
.card2-coment2{
    margin-left: 25px;
}
.card2-comentUser{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
}
.card2-comentUser p:nth-child(1){
   font-weight: 500;
}
.card2-comentUser p:nth-child(2){
   font-size: 11px;
}